<template>
  <b-button
    variant="transparent"
    class="text-center text-white rounded-0 d-flex flex-column align-items-center h-100 keyboard-number justify-content-center"
    @click="is_hold = !is_hold"
    block
    :disabled="disableHoldButton"
  >
    <span :class="`fe fe-pause-circle ${is_hold ? '' : 'text-danger'}`" />
    <small :class="`mb-0 text ${is_hold ? '' : 'text-danger'}`">Hold</small>
  </b-button>
</template>
<script>
  export default {
    props: {
      disableHoldButton: Boolean,
    },
    data() {
      return {
        is_hold: true,
      };
    },
  };
</script>
