<template>
  <div ref="agent">
    <b-card class="bg-dailer mb-0 border-0" no-body>
      <!-- Agent info  && timer starts here -->
      <div class="position-relative">
        <Header from="agent" />
        <!-- Timer starts here -->
        <Timer :app_name="app_name" :connection="connection" :phone_number="number" v-if="is_start_timer && status == 'connected'" ref="timer" />
      </div>
      <!-- Agent info  && timer ends here -->
      <div>
        <KeyPad :connection="connection" v-if="isKeypad == true" @digitPressed="digitPressed" @phoneNumber="changeNumber" ref="keypad" />
        <ConferenceCall
          :connection="connection"
          :twilioMute="twilioMute"
          @showkeypad="isKeypad = !isKeypad"
          @mutecall="muteCall()"
          @conferencecall="conferenceCall"
          v-if="connectedCall && !isKeypad"
          ref="conference_call_ref"
          :twilioConfig="twilioConfig"
        />
        <b-list-group>
          <b-list-group-item v-if="connectedCall && !isKeypad" class="rounded-0 p-0 border-left-0 border-right-0 border-light d-flex border-top-0">
            <div class="flex-grow-1 border-right">
              <Voicemail :disableVoicemailButton="config.disableVoicemailButton" ref="voicemail" :connection="connection" />
            </div>
            <div class="flex-grow-1 border-right">
              <PlayMessage :disablePlayButton="config.disablePlayButton" :connection="connection" />
            </div>
            <div class="flex-grow-1 ">
              <HoldCall :disableHoldButton="config.disablePlayButton" />
            </div>
          </b-list-group-item>
          <!-- v-if="connectedCall && !isKeypad" -->
          <b-list-group-item class="rounded-0 p-0 border-left-0 border-right-0 border-light d-flex border-bottom-0">
            <div :class="`flex-grow-1 border-right ${connectedCall && !isKeypad ? '' : 'keyboard-number'}`">
              <TransferCall :connection="connection" :domain_id="domain_id" @transfercall="transferCall()" v-if="connectedCall && !isKeypad" />
            </div>
            <div class="align-items-center border-right d-flex flex-column flex-grow-1 justify-content-center border-bottom-0">
              <HangupCall v-if="is_start_timer" :connection="connection" @disconnectcall="disconnect_call()" class="float-btn" />
              <b-button
                v-if="!is_start_timer"
                variant="success"
                type="button"
                @click="initiateCall"
                class="text-center text-white rounded-0 w-100 h-100 keyboard-number justify-content-center float-btn"
              >
                <span class="fe fe-phone" />
              </b-button>
            </div>
            <div class="flex-grow-1">
              <b-button
                variant="transparent"
                class="text-center text-white rounded-0 d-flex flex-column align-items-center h-100 keyboard-number justify-content-center"
                @click="showkeypad()"
                block
                v-if="isKeypad && connectedCall"
              >
                <span class="fe fe-grid" />
                <small>Keypad</small>
              </b-button>
            </div>
          </b-list-group-item>
        </b-list-group>
      </div>
      <!--  -->
    </b-card>
  </div>
</template>

<script>
  import { mapGetters } from "vuex";
  import Header from "./Header.vue";
  import Timer from "./Timer.vue";
  import KeyPad from "./KeyPad.vue";
  import ConferenceCall from "./agent/ConferenceCall.vue";
  import HangupCall from "./HangupCall.vue";
  import TransferCall from "./agent/TransferCall.vue";
  import HoldCall from "./agent/HoldCall.vue";
  import call from "../../js/dialer/call";
  import JWT from "../../js/dialer/jwt";
  import Voicemail from "./agent/Voicemail.vue";
  import PlayMessage from "./agent/PlayMessage.vue";
  import { bus } from "@/js/dialer/emit-bus";

  export default {
    components: { Header, Timer, KeyPad, ConferenceCall, HangupCall, TransferCall, HoldCall, Voicemail, PlayMessage },
    props: {
      number: String,
      app_name: {
        type: String,
      },
      is_make_call: {
        type: Boolean,
        default: true,
      },
      is_start_timer: {
        type: Boolean,
        default: true,
      },
      config: {
        type: Object,
        default: () => ({
          disablePlayButton: false,
          disableVoicemailButton: false,
          disableHoldButton: true,
        }),
      },
    },
    data() {
      return {
        capability_url: "",
        connection: {},
        twilioConnection: {},
        status: "connected",
        twilioMute: false,
        isKeypad: false,
        connectedCall: false,
        numbers: [],
        conference_call: false,
        domain_id: "",
        twilioConfig: {},
      };
    },
    computed: {
      ...mapGetters(["isTwilioReady", "device", "checkDeviceStatus"]),
    },
    watch: {
      isTwilioReady(data) {
        if (data && this.is_make_call) {
          this.initiateCall();
        }
        window.parent.postMessage({ type: "isTwilioDeviceReady" }, "*");
      },
      checkDeviceStatus(data) {
        if (data === "connect") {
          this.connectedCall = true;
          this.status = "connected";
        } else if (data == "disconnect") {
          this.connectedCall = false;
          this.is_start_timer = false;
          if (!this.is_make_call) {
            this.status = "connected";
            this.isKeypad = true;
          } else this.status = "";
        }
      },
    },
    mounted: async function() {
      bus.$on("incoming", (conn) => {
        // Incoming call check and post the messsage
        if (conn.type == "incoming" && !this.is_make_call) {
          this.twilioConnection = conn.conn;
          var incomming_data = {};
          incomming_data.direction = conn.conn.direction;
          incomming_data.from = conn.conn.parameters.From;
          incomming_data.callSid = conn.conn.parameters.CallSid;
          incomming_data.type = "IncomingCall";
          window.parent.postMessage(incomming_data, "*");
        }
        // Incoming call Accept
        if (conn.response == "Accept" && !this.is_make_call) {
          this.twilioConnection.accept();
          this.is_start_timer = true;
          this.status = "connected";
          this.connectedCall = true;
          this.number = conn.number;
          this.isKeypad = false;
          var show_dialer_obj = {};
          show_dialer_obj.type = "showDialer";
          window.parent.postMessage(show_dialer_obj, "*");
        }
        // Incoming call Reject
        if (conn.response == "Reject" && !this.is_make_call) {
          this.twilioConnection.reject();
        }
      });
      bus.$on("user_call_hangup", () => {
        this.disconnect_call();
      });
      let token = JWT.jwt_token;
      let response = await call.getCapabilityAgent(this.app_name);
      this.domain_id = token.tenant_id;

      // Set capability url and get twilio config details
      let config = await this.$store.dispatch("getToken", `${response.pref_value}?To=PBX_${this.domain_id}_${token.user_id}`);
      this.twilioConfig = config;

      if (!this.is_make_call) {
        this.isKeypad = true;
        this.status = "connected";
      }
    },
    methods: {
      showkeypad() {
        this.isKeypad = !this.isKeypad;
      },
      initiateCall() {
        if (this.number.length > 5 && this.number != "number") {
          window.self = this;
          this.connectedCall = true;
          this.isKeypad = false;
          this.$nextTick(() => {
            this.is_start_timer = true;
          });
          this.connection = this.device.connect({ To: this.number });
          this.$store.dispatch("twilioDeviceListeners");
          this.twilioConnection = this.connection;
          if (this.app_name) this.$emit("callConnected", this.twilioConnection);
        }
      },
      disconnect_call() {
        this.number = "";
        this.connectedCall = false;
        this.is_start_timer = false;
        this.status = "connected";
        this.device.disconnectAll();
      },
      muteCall() {
        this.twilioMute = !this.twilioMute;
        this.device.activeConnection().mute(this.twilioMute);
      },
      digitPressed(number) {
        if (this.is_start_timer) this.twilioConnection.sendDigits(number.toString());
        if (this.number == "number") this.number = "";
        this.number += number.toString();
      },
      conferenceCall(number) {
        this.conference_call = true;
        this.numbers.push(number);
      },
      changeNumber(number) {
        return (this.number = number);
      },
    },
  };
</script>
