<template>
  <div>
    <template v-if="connection">
      <b-button
        @click="show_transfer_call = !show_transfer_call"
        variant="transparent"
        class="text-center text-white rounded-0 d-flex flex-column align-items-center h-100 keyboard-number justify-content-center"
        v-b-modal.showtypes
        block
      >
        <span class="fe fe-phone-forwarded" />
        <small>Transfer</small>
      </b-button>
    </template>
    <b-modal v-if="show_transfer_call" size="sm" id="showtypes" body-class="p-0" hide-footer hide-header>
      <div class="p-3 m-2 d-flex align-items-center">
        <b-button-group class="w-100">
          <b-button
            size="sm"
            :variant="call_type == 'warm' ? 'primary' : 'white'"
            @click="
              call_type = 'warm';
              show_agents = true;
            "
          >
            <div class="d-flex align-items-center justify-content-center">
              <i class="fe fe-sun mr-2" />
              <p class="mb-0 text">Warm</p>
            </div>
          </b-button>
          <b-button
            size="sm"
            :variant="call_type == 'cold' ? 'primary' : 'white'"
            @click="
              call_type = 'cold';
              show_agents = true;
            "
          >
            <div class="d-flex align-items-center justify-content-center">
              <i class="fe fe-moon mr-2" />
              <p class="mb-0 text">Cold</p>
            </div>
          </b-button>
        </b-button-group>
      </div>
      <div class="px-3 mx-2 mb-2 pb-3" v-if="show_agents && show_transfer_call">
        <!-- Input starts here -->
        <b-form-input size="sm" placeholder="Enter phone number" class="font-weight-normal h3 mb-0 text-center" v-on:keyup.enter="transferCall(phone_number)" v-model="phone_number" />
        <!-- Or Text starts here -->
        <div class="d-flex flex-nowrap align-items-center my-2">
          <span class="border-top w-50"></span>
          <span class="small px-3 ">OR</span>
          <span class="border-top w-50"></span>
        </div>
        <!-- Dropdown starts here -->
        <b-dropdown block id="agents_list" text="Agents" variant="white" size="sm" toggle-class="rounded-top-0 d-flex justify-content-between align-items-center">
          <b-dropdown-item v-for="(item, index) in users" :key="index" @click="transferCall(`PBX_${domain_id}_${item.domain_user_id}`, 'client')">{{ item.user_name }}</b-dropdown-item>
        </b-dropdown>
        <b-overlay :show="isBusy" no-wrap opacity="0.5" />
      </div>
    </b-modal>
  </div>
</template>

<script>
  import conference from "../../../js/dialer/conference";
  export default {
    props: {
      connection: Object,
      domain_id: Number,
    },
    data() {
      return {
        users: [],
        show_transfer_call: false,
        show_agents: false,
        call_type: "",
        phone_number: "",
        isBusy: false,
      };
    },
    mounted: async function() {
      this.users = await conference.getAgents();
    },
    methods: {
      async transferCall(number, callType = "number") {
        this.isBusy = true;
        await conference.transferCall(this.connection.parameters.CallSid, this.call_type, callType, number);
        this.phone_number = "";
        this.isBusy = false;
        this.$bvModal.hide("showtypes");
      },
    },
  };
</script>
